  font-style: normal;
}

div.container, section.container {
  width: 86%;
  margin: 25px auto;
}

section.wide {
  width: 100vw !important;
  margin: 25px 0;
}

.center {
  text-align: center;
}

.v-container {
  display: table;
}

.v-container .vac {
  display: table-cell;
  vertical-align: middle;
}

li {
  list-style: none;
}

ul.inline li {
  display: inline-block;
}

